.ril-image-current {
  transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) !important; }

.ril__toolbar {
  background-color: transparent !important; }

.removeImage {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 12px;
  color: #fff;
  background: #ff5c5c;
  padding: 2px 3px; }

.post {
  border: 0;
  margin: 0 !important;
  background: transparent; }
  .post .post-thumb {
    height: 200px;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 10px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #222;
    box-shadow: 0 0 1px #00000094; }
  .post .w-108px {
    width: 108px; }
  .post h3 {
    font-size: 16px;
    font-weight: bold; }
  .post .splLi {
    background: transparent;
    padding: 2px 6px;
    border-right: 1px solid #fff !important; }
    .post .splLi img {
      width: 12px; }
  .post .splLiLast {
    background: transparent;
    padding: 2px 6px; }
  .post .post-inner {
    box-shadow: 0 0 4px #00000026;
    margin-bottom: 20px; }
  .post .post-content-area {
    min-height: 80px;
    align-items: flex-start !important; }
  .post .dropdown-menu {
    box-shadow: 0 0 10px #bfbfbf;
    padding: 8px; }
  .post .dropdown-item {
    padding: 2px 5px;
    font-size: 12px; }
    .post .dropdown-item img, .post .dropdown-item i {
      width: 11px;
      margin-right: 4px; }
  .post .post-meta {
    margin: 0 !important; }

.create-post-customize {
  width: 100%;
  display: block; }
  .create-post-customize ul {
    width: 100%; }

.blog-header {
  border: 1px solid #ddd;
  padding: 10px 20px;
  border-radius: 4px;
  margin-bottom: 15px;
  width: 100%; }
  .blog-header .tagbtn {
    border-radius: 4px;
    width: fit-content;
    max-width: none;
    text-align: center;
    background-color: #5931ea;
    color: #fff;
    padding: 5px;
    line-height: 16px; }
  .blog-header h1 {
    font-size: 28px;
    margin-top: 10px; }
  .blog-header p {
    font-size: 14px; }

.singleBlogPage .single {
  margin-bottom: 50px;
  border: 1px solid #ddd; }

.singleBlogPage .blog-cover {
  width: 100%;
  height: 250px;
  background-size: cover;
  display: flex;
  align-items: flex-start;
  justify-content: space-between; }
  .singleBlogPage .blog-cover .go-back {
    cursor: pointer;
    margin: 10px;
    padding: 10px 12px;
    background-color: #fff;
    border-radius: 50%; }
  .singleBlogPage .blog-cover .tags {
    display: flex;
    align-items: center;
    justify-content: center; }
    .singleBlogPage .blog-cover .tags .tag {
      background: #2257b9;
      color: #fff;
      cursor: pointer;
      border-radius: 3px;
      padding: 5px;
      min-width: 50px;
      text-align: center;
      font-weight: bold;
      margin: 10px; }

.singleBlogPage .blog-content .time {
  font-weight: bold;
  font-size: 14px; }

.singleBlogPage .blog-content h1 {
  font-size: 22px;
  font-weight: bolder;
  margin-bottom: 0px; }

.singleBlogPage .blog-content .blog-actions {
  color: #5931ea; }
  .singleBlogPage .blog-content .blog-actions span {
    font-size: 18px;
    padding: 2px; }

.singleBlogPage .blog-content .blog-text {
  margin-top: 20px; }

.singleBlogPage .comment-form input {
  width: 100%;
  border-radius: 10px; }

.singleBlogPage .comment-form button {
  margin: 0;
  margin-top: -4px;
  margin-right: -6px;
  height: 38px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  z-index: 0; }

.singleBlogPage .card-thumb {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-size: cover;
  background-position: center center; }

.singleBlogPage .paymentDiv {
  background-color: #fff;
  padding: 50px 20px;
  text-align: center; }

@media only screen and (max-width: 992px) {
  .blog-header, .single, .blog-widget {
    width: 94%;
    margin: 0 auto; }
  .blog-widget {
    width: 92%;
    margin-top: 10px; }
  .single > .row {
    margin: 0 !important; } }

.confirm-modal {
  padding: 10px; }

.deleted {
  background: #f1f1f1;
  border: 1px solid #f1f1f1;
  padding: 10px;
  color: #808080;
  text-align: center;
  border-radius: 4px; }
  .deleted .fa {
    margin-right: 10px; }
